import React from "react";
import { NavLink } from "react-router-dom";

import "./Footer.scss";

const Footer = () => {
  return (
    <footer className="main-footer">
      <div className="container">
        <div className="row">
          <div className="menu col-lg-8 col-md-12">
            <nav className="navbar navbar-expand-lg">
              <ul className="navbar-nav">
                <li className="nav-item">
                  <NavLink to="/" exact className="nav-link text-uppercase ">
                    Головна
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink to="/about" className="nav-link text-uppercase ">
                    Про нас
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink to="/services" className="nav-link text-uppercase ">
                    Послуги
                  </NavLink>
                </li>
                {/* <li className="nav-item">
                  <NavLink to="/portfolio" className="nav-link text-uppercase ">
                    Portfolio
                  </NavLink>
                </li> */}
                <li className="nav-item">
                  <NavLink to="/blog" className="nav-link text-uppercase ">
                    Блог
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    to="/contacts"
                    className="nav-link text-uppercase pr-0"
                  >
                    Контакти
                  </NavLink>
                </li>
              </ul>
            </nav>
          </div>
          <div className="license col-lg-4 col-md-12">
            <div>Created by akhouad 2016. All Rights Reserved</div>
            <ul>
              <li>
                <NavLink to="/">
                  <i className="fa fa-facebook" aria-hidden="true"></i>
                </NavLink>
              </li>
              <li>
                <NavLink to="/">
                  <i className="fa fa-twitter"></i>
                </NavLink>
              </li>
              <li>
                <NavLink to="/">
                  <i className="fa fa-linkedin-square"></i>
                </NavLink>
              </li>
              <li>
                <NavLink to="/">
                  <i className="fa fa-youtube-play"></i>
                </NavLink>
              </li>
              <li>
                <NavLink to="/">
                  <i className="fa fa-pinterest"></i>
                </NavLink>
              </li>
              <li>
                <NavLink to="/">
                  <i className="fa fa-instagram"></i>
                </NavLink>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
