import React, { useEffect, useState } from "react";
import Axios from "axios";
import { Switch, Route } from "react-router";

import "./PortfolioSection.scss";
// import AllProject from "../../pages/Portfolio/components/AllProject/AllProject";
// import CategoryProject from "../../pages/Portfolio/components/CategoryProject/CategoryProject";
import Links from "./Links/Links";

const PortfolioSection = () => {
  const [link, setLink] = useState([]);

  useEffect(() => {
    Axios.get(`https://react-akad.firebaseio.com/portfolio.json`)
      .then(response => {
        setLink(Object.keys(response.data.categories));
      })
      .catch(err => console.log("Error", err));
  }, [setLink]);

  return (
    <section className="portfolio">
      <div className="container">
        <div className="row">
          <div className="section-title">
            <span className="title-portfolio montserrat-text-title text-uppercase">
              Проекти
            </span>
            {/* <p>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua.
            </p> */}
          </div>
        </div>

        <div className="row">
          <div className="col-lg-3 category">
            <span className="montserrat-text-title text-uppercase">
              Choose category
            </span>

            <Links links={link} />
          </div>
          <div className="col-lg-9">
            <div className="portfolio_container">
              <ul id="stage"></ul>
              <div className="row">
                {/* <Switch>
                  <Route exact path="/portfolio/all" component={AllProject} />
                  <Route
                    path="/portfolio/:category"
                    component={CategoryProject}
                  />
                </Switch> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default PortfolioSection;
