import React from "react";

import "./PlanCard.scss";

const PlanCard = ({ planName, pricePlan, list }) => {
  return (
    <div className="card">
      <div className="card-body">
        <span className="card-title montserrat-text-title">{planName}</span>
        <div className="price text-uppercase montserrat-text">{pricePlan}</div>
        <ul className="list">
          <li>Lorem ipsum dolor sit amet</li>
          <li>Consectetuer adipiscing elit</li>
          <li>Sed diam nonummy</li>
          <li>Nibh euismod tincidunt</li>
          <li>Ut laoreet dolore</li>
          <li>Magna aliquam erat volutpat</li>
        </ul>
        <button className="montserrat-text text-uppercase">Get started</button>
      </div>
    </div>
  );
};

export default PlanCard;
