import React from "react";
import "./SiteHero2.scss";

const SiteHero2 = ({ componentBigTitle, componentSmallTitle }) => {
  return (
    <div className="site-hero_2">
      <div className="page-title">
        <div className="big-title_sh2  montserrat-text-title">
          {componentBigTitle}
        </div>
        <div className="small-title_sh2 text-uppercase montserrat-text">
          Home/{componentSmallTitle}
        </div>
      </div>
    </div>
  );
};

export default SiteHero2;
