import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "./TopNavbar.scss";

const TopNavbar = () => {
  const [isShow, setIsShow] = useState(false);
  // const [isSelected, setIsSelected] = useState(false);

  const handleMenuToggle = () => {
    setIsShow(!isShow);
  };

  const handleAddSelectedClass = event => {
    // console.log(event.target.parentNode.classList.contains("selected"));
    event.target.parentNode.classList.toggle("selected");
    // setIsSelected(!isSelected);
  };

  return (
    <header className="main-header">
      <div className="container">
        <nav className="menu-desktop navbar navbar-expand-lg navbar-light px-0 pt-4 montserrat-text">
          <div className="navbar-header">
            <NavLink
              to="/"
              exact
              className="nav navbar-brand text-uppercase "
              style={{ color: "#60606e" }}
            >
              MBK.
            </NavLink>
          </div>

          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav ml-auto">
              <li className="nav-item">
                <NavLink to="/" exact className="nav-link">
                  Головна
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to="/about" className="nav-link">
                  Про нас
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to="/services" className="nav-link ">
                  Послуги
                </NavLink>
              </li>
              {/* <li className="nav-item">
                <NavLink to="#" className="nav-link ">
                  Портфоліо
                </NavLink>

                <ul className="second-level">
                  <li className="nav-item">
                    <NavLink to="/portfolio">Portfolio list</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/portfolio/single-project-1">
                      Single project 1
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/portfolio/single-project-2">
                      Single project 2
                    </NavLink>
                  </li>
                </ul>
              </li> */}
              <li className="nav-item">
                <NavLink to="#" className="nav-link ">
                  Блог
                </NavLink>

                <ul className="second-level">
                  <li className="nav-item">
                    <NavLink to="/blog">Статті</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/blog/single-post">Single post</NavLink>
                  </li>
                </ul>
                <ul className="second-level">
                  <li className="nav-item"></li>
                  <li className="nav-item"></li>
                </ul>
              </li>
              <li className="nav-item">
                <NavLink to="/contacts" className="nav-link pr-0">
                  Контакти
                </NavLink>
              </li>
            </ul>
          </div>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
        </nav>

        <div className="menu-mobile px-0 pt-4">
          <div className="logo">
            <NavLink
              to="/"
              exact
              className="nav navbar-brand text-uppercase montserrat-text"
              style={{ color: "#60606e" }}
            >
              MBK.
            </NavLink>
          </div>
          <div className="menu-icon" onClick={handleMenuToggle}>
            <div className="line"></div>
            <div className="line"></div>
            <div className="line"></div>
          </div>

          <nav className={`mobile-nav ${isShow ? "d-block" : "d-none"} `}>
            <ul className="navbar-nav first-level">
              <li className="nav-item">
                <NavLink to="/" exact className="nav-link">
                  Головна
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to="/about" className="nav-link">
                  Про нас
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to="/services" className="nav-link">
                  Послуги
                </NavLink>
              </li>
              {/* <li className="nav-item" onClick={handleAddSelectedClass}>
                <NavLink to="#" className="nav-link">
                  Портфоліо
                </NavLink>

                <ul className="second-level down">
                  <li className="nav-item">
                    <NavLink to="/portfolio">Portfolio list</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/portfolio">Single project 1</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/portfolio">Single project 2</NavLink>
                  </li>
                </ul>
              </li> */}
              <li className="nav-item" onClick={handleAddSelectedClass}>
                <NavLink to="#" className="nav-link  ">
                  Блог
                </NavLink>

                <ul className="second-level down">
                  <li className="nav-item">
                    <NavLink to="/blog">Статті</NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/blog">Single post</NavLink>
                  </li>
                </ul>
              </li>
              <li className="nav-item">
                <NavLink to="/contacts" className="nav-link pr-0">
                  Контакти
                </NavLink>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </header>
  );
};

export default TopNavbar;
