import React from "react";
import "./NewsLetter.scss";

const NewsLetter = () => {
  return (
    <section className="news-letter">
      <div className="container">
        <div className="row">
          <div className="col-md-6 col-sm-12 work-together">
            <span className="montserrat-text-title">
              Думаєш ми круті? Давай працювати разом
            </span>
            <button className="montserrat-text text-uppercase">
              Get in touch
            </button>
          </div>
          <div className="col-md-6 col-sm-12 form-block">
            <div className="white-section">
              <span className="text-uppercase montserrat-text">
                Залишайса завжди в курсі подій
              </span>
              <p>
                Підпишись на розсилку статей на різні тематики, та завжди
                залишайся в курсі подій.
              </p>
              <form className="form">
                <div>
                  <input
                    type="email"
                    className="monserrat-text"
                    placeholder="YOUR EMAIL"
                  />
                </div>
                <button className="montserrat-text text-uppercase">Send</button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default NewsLetter;
