import React from "react";
import { NavLink } from "react-router-dom";

import "./Category.scss";

const Category = () => {
  return (
    <ul className="category">
      <li>
        <NavLink to="#">
          <span className="category-span">Busines</span>
          <span className="category-count">15</span>
        </NavLink>
      </li>
      <li>
        <NavLink to="#">
          <span className="category-span">Photograpy</span>
          <span className="category-count">22</span>
        </NavLink>
      </li>
      <li>
        <NavLink to="#">
          <span className="category-span">Journal</span>
          <span className="category-count">27</span>
        </NavLink>
      </li>
      <li>
        <NavLink to="#">
          <span className="category-span">Web development</span>
          <span className="category-count">30</span>
        </NavLink>
      </li>
    </ul>
  );
};

export default Category;
