import React from "react";
import "./Agency.scss";
import { NavLink } from "react-router-dom";

const Agency = () => {
  return (
    <div className="container">
      <div className="agency">
        <div className="row">
          <div className="col-lg-5 col-md-12">
            <img src="https://picsum.photos/475/400?grayscale" alt="" />
          </div>
          <div className="offset-lg-1 col-lg-6 col-md-12">
            <div className="section-title">
              <span className="montserrat-text-title">Наша історія</span>
              <p>
                Креативна агенція «МиВашКреатив» починає свою історію з 2010 р.
                Засновниками агентства є Іван Іванов і Петро Петров, які
                беззмінно керують компанією до теперішнього часу. Після
                університету Іван Іванов задумав відкрити свою справу, у якій
                він би розумів. Разом зі своїм другом Петром Петровим вони
                віришили заснувати креативну агенцію.
              </p>
            </div>

            <div className="text-right">
              <button className="montserrat-text text-uppercase">
                <NavLink
                  to="/about"
                  style={{ textDecoration: "none", color: "white" }}
                >
                  Читати більше
                </NavLink>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Agency;
