import React from "react";
import { NavLink } from "react-router-dom";

import SiteHero2 from "../../components/SiteHero2/SiteHero2";
import "./Contacts.scss";

const Contacts = () => {
  return (
    <>
      <SiteHero2
        componentBigTitle={"Контакти"}
        componentSmallTitle={"Contact"}
      />

      <section className="contacts">
        <div className="container">
          <div className="row">
            <div className="col-lg-6 col-md-12">
              <form>
                <div className="row">
                  <div className="col-lg-6">
                    <div className="input-wrapper">
                      <input
                        className="input form-control"
                        type="text"
                        placeholder="YOUR NAME"
                      />
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <div className="input-wrapper">
                      <input
                        className="input form-control"
                        type="email"
                        placeholder="YOUR EMAIL"
                      />
                    </div>
                  </div>
                  <div className="col-lg-12">
                    <div className="input-wrapper">
                      <input
                        className="input form-control"
                        type="text"
                        placeholder="OBJECT"
                      />
                    </div>
                  </div>
                  <div className="col-lg-12">
                    <div className="textarea-wrapper">
                      <textarea
                        className="form-control"
                        cols="30"
                        rows="10"
                        placeholder="MESSAGE"
                      ></textarea>
                    </div>
                  </div>

                  <div className="col-lg-12">
                    <button className="montserrat-text text-uppercase">
                      Send
                    </button>
                  </div>
                </div>
              </form>
              <div className="contact-info">
                <div className="contact-title">
                  <span>Contact info</span>
                  {/* <p>
                    Lorem ipsum dolor sit amet, conse adipisicing elit. Libero
                    incidunt quod ab mollitia quia dolorum conse.
                  </p> */}
                </div>
                <div className="address">
                  <span>13D, Functional apartment, Unique colony, </span> <br />
                  <span>Agadir 86360</span> <br />
                  <span>+212 124-566-780</span> <br />
                  <span>+212 124-566-780</span> <br />
                  <p className="email-text">email@website.com</p>
                </div>
                <div className="social">
                  <ul>
                    <li>
                      <NavLink to="#">
                        <i className="fa fa-facebook" aria-hidden="true"></i>
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="#">
                        <i className="fa fa-twitter"></i>
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="#">
                        <i className="fa fa-linkedin-square"></i>
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="#">
                        <i className="fa fa-youtube-play"></i>
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="#">
                        <i className="fa fa-pinterest"></i>
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="#">
                        <i className="fa fa-instagram"></i>
                      </NavLink>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-md-12">
              <div className="maps">
                <iframe
                  title="Map"
                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2596.769138072943!2d26.58769558516407!3d48.67674887057783!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zNDjCsDQwJzM2LjMiTiAyNsKwMzUnMTkuOSJF!5e1!3m2!1sru!2sua!4v1582198390476!5m2!1sru!2sua"
                  width="100%"
                  height="100%"
                  frameborder="0"
                  style={{ border: 0 }}
                  allowfullscreen=""
                ></iframe>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Contacts;
