import React from "react";
import { Route } from "react-router";

import "./Blog.scss";
import SiteHero2 from "../../components/SiteHero2/SiteHero2";
import Post from "./components/Post/Post";
import Sidebar from "./components/Sidebar/Sidebar";

const Blog = () => {
  return (
    <>
      <SiteHero2 componentBigTitle={"Блог"} componentSmallTitle={"Blog"} />
      <section>
        <div className="container">
          <div className="row">
            <div className="col-lg-8 col-md-12">
              <Route
                path="/blog"
                render={() => (
                  <>
                    <Post />
                    <Post />
                    <Post />
                  </>
                )}
              />
            </div>
            <div className="sidebar col-lg-3 offset-lg-1 col-md-12">
              <Sidebar />
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Blog;
