import React from "react";

import "./LatestPosts.scss";
import { NavLink } from "react-router-dom";

const LatestPosts = () => {
  return (
    <div className="latest-posts">
      <div className="post-wrapper">
        <div className="post-image">
          <img src="https://picsum.photos/seed/picsum/80/80" alt="" />
        </div>
        <div className="post-title">
          <span className="span-title montserrat-text">
            <NavLink to="#">Magna mollis ultricies</NavLink>
          </span>
          <span className="span-date">3th oct 2012</span>
        </div>
      </div>
    </div>
  );
};

export default LatestPosts;
