import React from "react";
import { Route, Switch } from "react-router";

import Home from "./pages/Home/Home";
import About from "./pages/About/About";
import Services from "./pages/Services/Services";
import Portfolio from "./pages/Portfolio/Portfolio";
import Blog from "./pages/Blog/Blog";
import Contacts from "./pages/Contacts/Contacts";
import SingleProject1 from "./pages/Project/components/SingleProject1/SingleProject1";
import SingleProject2 from "./pages/Project/components/SingleProject2/SingleProject2";
import SinglePost from "./pages/SinglePost/SinglePost";

export default () => {
  return (
    <Switch>
      <Route path="/" exact component={Home} />
      <Route path="/about" component={About} />
      <Route path="/services" component={Services} />
      <Route path="/portfolio" exact component={Portfolio} />
      <Route path="/portfolio/single-project-1" component={SingleProject1} />
      <Route path="/portfolio/single-project-2" component={SingleProject2} />
      <Route path="/portfolio/:category" exact component={Portfolio} />
      <Route path="/portfolio/:category/:project" exact component={Portfolio} />
      <Route path="/blog" exact component={Blog} />
      <Route path="/blog/single-post" component={SinglePost} />
      <Route path="/contacts" component={Contacts} />
    </Switch>
  );
};
