import React from "react";
import { NavLink, Switch, Route } from "react-router-dom";

import "./Posts.scss";
import LatestPosts from "../LatestPosts/LatestPosts";
import PopularPosts from "../PopularPosts/PopularPosts";

const Posts = () => {
  return (
    <div className="widget widget-posts">
      <span className="widget-title montserrat-text">Posts</span>
      <div className="tab">
        <div class="tabs">
          <div class="tab-2">
            <label for="tab2-1">Latest</label>
            <input id="tab2-1" name="tabs-two" type="radio" checked="checked" />
            <div>
              <LatestPosts />
              <LatestPosts />
              <LatestPosts />
            </div>
          </div>
          <div class="tab-2">
            <label for="tab2-2">Popular</label>
            <input id="tab2-2" name="tabs-two" type="radio" />
            <div>
              <PopularPosts />
              <PopularPosts />
              <PopularPosts />
            </div>
          </div>
        </div>
        {/* <nav>
          <NavLink
            className="montserrat-text"
            to="/blog"
            exact
            activeStyle={{ borderBottom: "2px solid #7beec7" }}
          >
            Latest
          </NavLink>
          <NavLink
            className="montserrat-text"
            to="/blog/popular"
            exact
            activeStyle={{ borderBottom: "2px solid #7beec7" }}
          >
            Popular
          </NavLink>
        </nav>

        <Switch>
          <Route
            exact
            path="/blog"
            render={() => (
              <>
                <LatestPosts />
                <LatestPosts />
                <LatestPosts />
              </>
            )}
          />
          <Route
            path="/blog/popular"
            render={() => (
              <>
                <PopularPosts />
                <PopularPosts />
                <PopularPosts />
              </>
            )}
          />
        </Switch> */}
      </div>
    </div>
  );
};

export default Posts;
