import React from "react";
import { NavLink } from "react-router-dom";

import "./PopularPosts.scss";

const PopularPosts = () => {
  return (
    <div className="popular-posts">
      <div className="post-wrapper">
        <div className="post-image">
          <img src="https://picsum.photos/seed/picsum/80/80" alt="" />
        </div>
        <div className="post-title">
          <span className="span-title montserrat-text">
            <NavLink to="#">ultricies mollis Magna</NavLink>
          </span>
          <span className="span-date">3th oct 2012</span>
        </div>
      </div>
    </div>
  );
};

export default PopularPosts;
